<script>
import { mask } from "vue-the-mask";
import store from "@/store";

export default {
  name: "Security",

  directives: { mask },

  data: () => ({
    security: {},
    shownPassword: {
      new: false,
      confirm: false
    },
    errorMessages: {},
    rules: {
      required: [v => !!v || "Поле обязательно для заполнения!"]
    },
    selects: {
      doctors: []
    },
    credentials: store.getters["authentication/credentials"]
  }),

  computed: {
    computedRules() {
      return {
        minNumber: this.min,
        samePassword: this.samePassword
      };
    }
  },

  async beforeMount() {
    await this.getData();
  },

  methods: {
    min(number) {
      return v => !v || Number(v) >= number || `Минимальное значение ${number}`;
    },
    samePassword() {
      return v => v === this.security.password || `Пароль не совпадает!`;
    },
    async getData() {
      try {
        if (this.credentials.user.position === "patient") {
          this.security = await this.$API.profile().getSettings();
          this.selects.doctors = await this.$API.profile().getDoctors();
          this.security.doctors = this.selects.doctors
            .filter(item => item.is_allowed)
            .map(item => item.value);
        } else {
          this.security = {};
        }
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    async changeSecurity() {
      try {
        await this.$API
          .profile()
          .changeSecurity(Number(this.$route.params.id), this.security);
        await this.$store.dispatch("alert/showSuccess", "Success");
        this.security = {
          ...this.security,
          password: undefined,
          old_password: undefined,
          password_confirmation: undefined
        };
        this.$refs.form.resetValidation();
      } catch (e) {
        if (e.hasOwnProperty("errors")) {
          this.errorMessages = e.errors;
          setTimeout(() => {
            this.errorMessages = {};
          }, 2000);
        }
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    async changePassword() {
      try {
        if (this.security.password === this.security.password_confirmation) {
          await this.$API
            .profile()
            .changePassword(Number(this.$route.params.id), this.security);
          await this.$store.dispatch(
            "alert/showSuccess",
            this.$t("profile.alert.success")
          );
          this.security = {
            ...this.security,
            password: undefined,
            old_password: undefined,
            password_confirmation: undefined
          };
          this.$refs.form.resetValidation();
        }
      } catch (e) {
        if (e.hasOwnProperty("errors")) {
          this.errorMessages = e.errors;
          setTimeout(() => {
            this.errorMessages = {};
          }, 2000);
        }
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    getPasswordType(key) {
      if (this.shownPassword[key]) {
        return "text";
      }
      return "password";
    },
    togglePasswordType(key) {
      this.shownPassword[key] = !this.shownPassword[key];
    }
  }
};
</script>

<template>
  <v-card elevation="1">
    <v-card-title class="bordered">
      <span>
        {{ $t("profile.headers.security-title") }}
      </span>
    </v-card-title>
    <v-card-subtitle>
      {{ $t("profile.headers.security-subtitle") }}
    </v-card-subtitle>
    <v-card-text class="mt-3">
      <v-form ref="form" @submit.prevent.stop>
        <v-row>
          <v-col cols="12">
            <v-row class="mt-5">
              <v-col cols="12">
                <v-text-field
                  type="password"
                  :errorMessages="errorMessages.old_password"
                  v-model="security.old_password"
                  dense
                  prepend-icon="mdi-lock"
                  :label="$t('auth.fields.password')"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="security.password"
                  dense
                  :errorMessages="errorMessages.password"
                  prepend-icon="mdi-lock-plus"
                  :label="$t('auth.fields.new_password')"
                  :type="getPasswordType('new')"
                >
                  <template #append>
                    <v-btn
                      @click="togglePasswordType('new')"
                      color="primary"
                      fab
                      x-small
                      text
                    >
                      <v-icon>mdi-eye</v-icon>
                    </v-btn>
                  </template>
                </v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :errorMessages="errorMessages.password_confirmation"
                  v-model="security.password_confirmation"
                  dense
                  prepend-icon="mdi-lock-reset"
                  :label="$t('auth.fields.password_confirmation')"
                  :type="getPasswordType('confirm')"
                >
                  <template #append>
                    <v-btn
                      @click="togglePasswordType('confirm')"
                      color="primary"
                      fab
                      x-small
                      text
                    >
                      <v-icon>mdi-eye</v-icon>
                    </v-btn>
                  </template>
                </v-text-field>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions class="d-flex pt-0">
      <v-spacer></v-spacer>
      <v-btn @click="changePassword()" class="py-0" color="primary" depressed>
        {{ $t("global-buttons.save") }}
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

